// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import Loading from 'vue-loading-overlay'; //vue-loading-overlay
import 'bootstrap';
// NPM 
// 
import App from './App';
import router from './router';
import 'vue-loading-overlay/dist/vue-loading.css';
import './bus';
import currencyFilter from './filters/currency';
import dateFilter from './filters/date';

import VeeValidate from 'vee-validate';
import zhTWValidate from 'vee-validate/dist/locale/zh_TW';

Vue.config.productionTip = false;
Vue.use(VueAxios, axios);
Vue.use(VeeValidate);
VeeValidate.Validator.localize('zh_TW', zhTWValidate); //VeeValidate
axios.defaults.withCredentials = true;

Vue.component('Loading',Loading) //Loading
Vue.filter('currency', currencyFilter);
Vue.filter('date',dateFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

//
router.beforeEach((to, from, next) => {
  console.log('to',to,'from',from,'next',next);
  // ...
  if(to.meta.requiresAuth){
    console.log("");
    const api = `${process.env.APIPATH}/api/user/check`; //signin api
    axios.post(api).then((response) => { //postvm.user
        console.log(response.data);
        if(response.data.success){
          next(); // 
        }else{
          next({ // next(){}
            path:'/login',//login page
          });
        }
    });
  }else{
    next();
  };
})



