<template>
    <div>
      <loading :active.sync="isLoading"></loading> 
      <!-- loading-overlay -->
        <div class="text-right mt-4">
          <button class="btn-primary" @click="openModal(true)"></button> 
          <!-- openModal(isNew)Modal -->
        </div>
        <table class="table mt-4">
          <thead>
            <th width="120"></th>
            <th width></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="100"></th>
            <th width="140"></th>
          </thead>
          <tbody>
            <tr v-for="(item, key) in products" :key="item.id">
              <td>{{item.category}}</td>
              <td>{{item.title}}</td>
              <td class="text-right">
                {{item.origin_price | currency}} 
              </td>
              <td class="text-right">
                {{item.price | currency}}
              </td>
              <td class="text-right">
                <span v-if="item.is_enable" class="text-success"></span>
                <span v-else=""></span>
              </td>
              <td>
                <!-- <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button> -->
                <div class="btn-group">
                  <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
                  <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)"></button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>

        <nav aria-label="Page navigation example">
          <ul class="pagination">
            <li class="page-item">
              <a class="page-link" href="#" aria-label="Previous" :class="{'disabled': !pagination.has_pre}" @click.prevent="getProducts(pagination.current_page - 1)"> 
                <span aria-hidden="true">&laquo;</span>
                <span class="sr-only">Previous</span>
              </a>
            </li>
            <li class="page-item" v-for="page in pagination.total_pages" :key="page"
            :class="{'active':pagination.current_page === page}">
              <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a>
            </li>
            <li class="page-item">
              <a class="page-link" href="#" aria-label="Next" :class="{'disabled': !pagination.has_next}" @click.prevent="getProducts(pagination.current_page + 1)">
                <span aria-hidden="true">&raquo;</span>
                <span class="sr-only">Next</span>
              </a>
            </li>
          </ul>
        </nav>

        <Pagination :pages="pagination" @emitPages="getProducts"></Pagination>

        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
              <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                  <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="row">
                  <div class="col-sm-4">
                    <div class="form-group">
                        <label for="image"></label>
                        <input type="text" class="form-control" id="image" v-model="tempProduct.imageUrl" placeholder="">
                    </div>
                    <div class="form-group">
                      <label for="customFile"> 
                          <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                          <!-- fileUploading -->
                      </label>
                      <input type="file" id="customFile" class="form-control" @change="uploadFile()" ref="files">
                    </div>
                    <img class="img-fluid" :src="tempProduct.imageUrl" alt="">
                  </div>
                  <div class="col-sm-8">
                    <div class="form-group">
                      <label for="title"></label>
                      <input type="text" class="form-control" id="title" v-model="tempProduct.title" placeholder="">
                    </div>
                    <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="category"></label>
                        <input type="text" class="form-control" id="category" v-model="tempProduct.category" placeholder="">
                      </div>
                      <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit" placeholder="">
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="form-group col-md-6">
                        <label for="origin_price"></label>
                        <input type="number" class="form-control" id="origin_price" v-model="tempProduct.origin_price" placeholder="">
                      </div>
                      <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="number" class="form-control" id="price" v-model="tempProduct.price" placeholder="">
                      </div>
                    </div>
                    <hr>
                    <div class="form-group">
                      <label for="description"></label>
                      <textarea type="text" class="form-control" id="description" v-model="tempProduct.description" placeholder=""></textarea>
                    </div>
                    <div class="form-group">
                      <label for="content"></label>
                      <textarea type="text" class="form-control" id="content" v-model="tempProduct.content" placeholder=""></textarea>
                    </div>
                    <div class="form-group">
                      <div class="form-check">
                        <input class="form-check-input" type="checkbox" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0" id="is_enabled">
                        <label class="form-check-label" for="is_enabled">
                            
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-primary" @click="updateProduct"></button>
              </div>
            </div>
          </div>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
              <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                  <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body"> <strong class="text-danger">{{ tempProduct.title }}</strong> ()
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                <button type="button" class="btn btn-danger"
                  @click="delProduct"></button>
              </div>
            </div>
          </div>
        </div>

    </div>
</template>

<script>
import $ from 'jquery'; //jQuery$
import Pagination from '../Pagination';

export default {
    data(){
      return {
          products:[], //return
          pagination:[], //
          tempProduct:{},
          isNew : false,
          isLoading: false, //loading-overlay
          status: { //font-awesome
            fileUploading: false,
          },
      };
    },
    methods:{
      getProducts(page = 1){ //ES6: page1
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`; //api
        const vm = this;
        vm.isLoading = true; //getProductsisLoading
        this.$http.get(api).then((response) => {
          console.log("getProducts:"+response.data);
          // if(response.data.success){
            vm.isLoading = false; //getProductsisLoading
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
          // }
        });
      },
      openModal(isNew,item){ //isNew(item) 
        if(isNew){
          this.tempProduct={};
          this.isNew=true;
        }else{
          this.tempProduct=Object.assign({},item); //Object.assignitemtempProduct
          this.isNew=false;
        }
        $('#productModal').modal('show'); //bootstrapdocumentmethod
      },
      updateProduct(){
        let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`; //api
        let httpMethod = 'post';
        const vm = this;
        if(!vm.isNew){
          api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
          httpMethod = 'put';
        }
        this.$http[httpMethod](api, {data:vm.tempProduct}).then((response) => {
          //{data:vm .tempProduct}vm.tempProductdata
        console.log(response.data);
        if(response.data.success){
          $('#productModal').modal('hide');
          vm.getProducts();
        }else{
          $('#productModal').modal('hide');
          vm.getProducts();
          console.log('');
        }
        });
      },
      openDelProductModal(item) {
      const vm = this;
      $('#delProductModal').modal('show'); //delProductModal
      vm.tempProduct = Object.assign({}, item); //temProductitem
      },
      delProduct() {
        const vm = this;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        this.$http.delete(url).then((response) => {
          console.log(response, vm.tempProduct);
          $('#delProductModal').modal('hide'); //delPrroductModal
          vm.isLoading = false;
          this.getProducts();
        });
      },
      uploadFile(){
        console.log(this);
        const uploadedFile = this.$refs.files.files[0]; // files
        const vm = this;
        const formData = new FormData(); //formDataWebApi
        formData.append('file-to-upload', uploadedFile); //.append()'file-to-upload'
        // formDatadocument: https://developer.mozilla.org/en-US/docs/Web/API/FormData/FormData
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`; //
        vm.status.fileUploading = true; //uploadFile()AJAX
        this.$http.post(url,formData,{
          headers:{
            'Content-Type': 'multipart/form-data', //formData
          },
        }).then((response) => {
          console.log(response.data);
          vm.status.fileUploading = false; //uploadFile()AJAX
          if(response.data.success){ //
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log(vm.tempProduct); //vuegettersetter
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl); 
          }else{
            this.$bus.$emit('message:push', response.data.message, 'danger');
          }
        });
      },
    },
    components: {
    Pagination,
  },
    created(){ //creted hook getProducts();
      this.getProducts();
      // this.$bus.$emit('message:push','','success'); //emit(,,BS)
    },
}
</script>


